import React, { useEffect, useRef, useState, useMemo } from 'react';
import { useLocation, Link } from 'react-router-dom';
import useActiveWeb3React from 'hooks/useActiveWeb3React';
import { useTranslation } from 'contexts/Localization';
import { ButtonMenu, ButtonMenuItem, Button, Flex, Text } from '@pancakeswap/uikit';
import ConnectWalletButton from 'components/ConnectWalletButton';
import { ethers } from 'ethers'; 
import { createClient } from '@supabase/supabase-js';
import Gift from '../Vote2/Gift';
import EmojiPicker from './EmojiPicker';  // Import the EmojiPicker component
import { ChatBox, Input, Container, Title, TopListTitle } from './StyledComponents';
import NumberPurchaseABI from './NumberPurchaseABI.json';
import contractABI from './contractABI.json';
import GiftABI from './GiftABI.json';
import TokenABI from './TokenABI.json';
import useWalletConnect from './usewalletconnect'; // A fenti hook importÃ¡lÃ¡sa


const supabaseUrl = process.env.REACT_APP_SUPABASE_URL;
const supabaseKey = process.env.REACT_APP_SUPABASE_KEY;

const supabase = createClient(supabaseUrl, supabaseKey);

const Profile = () => {
  const { account, library } = useActiveWeb3React();
    const [messages, setMessages] = useState([]);
      const [provider, setProvider] = useState<ethers.providers.Web3Provider | null>(null); // Web3Provider Ã¡llapot
    const [newMessage, setNewMessage] = useState(''); 
    const [cooldown, setCooldown] = useState(0);
    const [lastCollector, setLastCollector] = useState(null);
    const [lastCollectedAmount, setLastCollectedAmount] = useState(null);
    const chatBoxRef = useRef(null);
    const location = useLocation();
    const [topList, setTopList] = useState([]);
    const [top10Addresses, setTop10Addresses] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [names, setNames] = useState({});
    const [userNumbers, setUserNumbers] = useState({});
    const [isSearching, setIsSearching] = useState(false);
    const [searchQuery, setSearchQuery] = useState('');
    const [emojiPickerVisible, setEmojiPickerVisible] = useState(false);
    const [image, setImage] = useState(null);
    const [giftBalance, setGiftBalance] = useState(0);
    const [contract, setContract] = useState<ethers.Contract | null>(null);
const [contract2, setContract2] = useState<ethers.Contract | null>(null);
const [giftContract, setGiftContract] = useState<ethers.Contract | null>(null);
const [tokenContract, setTokenContract] = useState<ethers.Contract | null>(null);

    const itemsPerPage = 25;
    const [currentName, setCurrentName] = useState<string | null>(null); // vagy csak string ha biztosan van name
const [totalUsers, setTotalUsers] = useState<number>(0);
    const { t } = useTranslation();
    const contractAddress = '0x674e3eDc898Aa4b486Bd2E5aa2D1C8653677F471';  
    const contractAddress2 = '0xBE8C7E554C89e064ED7229952036c3678cd10180';
    const giftAddress = '0xC8B19e19f52CE07bf9c4745caA4aAC87ab199cDD';  
    const tokenContractAddress = '0xaDAaE082237cB1772c9e079dB95c117E6Dd0C5aF';
    const fileInputRef = useRef(null);
    useWalletConnect(setProvider, () => setProvider(null));

    useEffect(() => {
      if (library) {
          const signer = library.getSigner();
          setContract(new ethers.Contract(contractAddress, contractABI, signer));
          setContract2(new ethers.Contract(contractAddress2, NumberPurchaseABI, signer));
          setGiftContract(new ethers.Contract(giftAddress, GiftABI, signer));
          setTokenContract(new ethers.Contract(tokenContractAddress, TokenABI, signer));
      }
  }, [library]);
  
    useEffect(() => {
      const fetchGiftBalance = async () => {
        try {
          const balance = await tokenContract.balanceOf(giftAddress);
          const formattedBalance = ethers.utils.formatUnits(balance, 18);
          setGiftBalance(parseFloat(formattedBalance));
        } catch (error) {
          console.error('Error fetching Gift contract balance:', error);
        }
      };

      const fetchLastCollectedData = async () => {
        try {
          const lastCollector = await giftContract.lastCollector();
          const lastCollectedAmount = await giftContract.lastCollectedAmount();
          const formattedAmount = ethers.utils.formatUnits(lastCollectedAmount, 18);
          setLastCollector(lastCollector);
          setLastCollectedAmount(parseFloat(formattedAmount));
        } catch (error) {
          console.error('Error fetching last collected data:', error);
        }
      };
    
      fetchGiftBalance();
      fetchLastCollectedData();
    }, [tokenContract, giftContract]);

    const collectTokens = async () => {
      try {
        const tx = await giftContract.collect();
        await tx.wait();
        alert('Tokens collected successfully!');
        const lastCollector = await giftContract.lastCollector();
        const lastCollectedAmount = await giftContract.lastCollectedAmount();
        const formattedAmount = ethers.utils.formatUnits(lastCollectedAmount, 18);
        setLastCollector(lastCollector);
        setLastCollectedAmount(parseFloat(formattedAmount));
      } catch (error) {
        console.error('Error collecting tokens:', error);
        alert('Failed to collect tokens');
      }
    };

    useEffect(() => {
      if (chatBoxRef.current) {
        chatBoxRef.current.scrollTop = chatBoxRef.current.scrollHeight;
      }
    }, [account, messages]); 

    useEffect(() => {
      const fetchMessages = async () => {
        const { data, error } = await supabase
          .from('messages')
          .select('*')
          .order('timestamp', { ascending: true });
    
        if (error) {
          console.error('Error fetching messages:', error);
        } else {
          setMessages(data);
          calculateTopList(data);
        }
      };
    
      fetchMessages();
    }, []);

    useEffect(() => {
      const channel = supabase
        .channel('public:messages')
        .on('postgres_changes', { event: 'INSERT', schema: 'public', table: 'messages' }, payload => {
          const newMessage = payload.new;
          setMessages(prevMessages => {
            const updatedMessages = [...prevMessages, newMessage];
            calculateTopList(updatedMessages);
            return updatedMessages;
          });
        })
        .subscribe();
    
      return () => {
        supabase.removeChannel(channel);
      };
    }, []);
      const userList = useMemo(() => {
    const usersFromMessages = messages.map(msg => msg.sender);
    const usersFromTopList = topList.map(([user]) => user);
    return Array.from(new Set([...usersFromMessages, ...usersFromTopList])); // Egyedi felhasznÃ¡lÃ³k Ã¶sszegyÅ±jtÃ©se
  }, [messages, topList]);

    useEffect(() => {
      if (!account || userList.length === 0) return;
    
      const fetchUserData = async () => {
        const newNames = {};
        const newNumbers = {};
    
        await Promise.all(userList.map(async (user) => {
          try {
            // PrÃ³bÃ¡ljuk meg elÅ‘szÃ¶r a Supabase-bÅ‘l lekÃ©rni az adatokat
            const { data: existingData, error: fetchError } = await supabase
              .from('user_data')
              .select('*')
              .eq('account', user)
              .single();
    
            if (fetchError && fetchError.code !== 'PGRST116') {
              console.error(`Error fetching data from Supabase for ${user}:`, fetchError);
              return;
            }
    
            if (existingData) {
              // Ha vannak adatok a Supabase-ben, hasznÃ¡ljuk azokat
              newNames[user] = existingData.name;
              newNumbers[user] = existingData.number;
            } else {
              // Ha nincsenek adatok a Supabase-ben, kÃ©rjÃ¼k le a smart contractbÃ³l
              const name = await contract.getName(user);
              const userPurchase = await contract2.userPurchases(user);
              const userNumber = userPurchase.number.toNumber();
    
              newNames[user] = name;
              newNumbers[user] = userNumber;
    
              // Az Ãºj adatokat elmentjÃ¼k a Supabase-be
              const { error } = await supabase.from('user_data').insert([{ account: user, name, number: userNumber }]);
              if (error) {
                console.error(`Error inserting data to Supabase for ${user}:`, error);
              } else {
                console.log(`User data inserted to Supabase for ${user} successfully`);
              }
            }
          } catch (error) {
            console.error(`Error fetching data for ${user}:`, error);
          }
        }));
    
        setNames(newNames);
        setUserNumbers(newNumbers);
      };
    
      fetchUserData();
    }, [account, userList, contract, contract2]);
    
    

    useEffect(() => {
      if (cooldown > 0) {
        const timer = setTimeout(() => setCooldown(cooldown - 1), 1000);
        return () => clearTimeout(timer);
      }
      return undefined;
    }, [cooldown]);
  
    const toggleSearchMode = () => {
      setIsSearching(!isSearching);
      setSearchQuery('');
    };
  
    const handleSearchInputChange = (e) => {
      setSearchQuery(e.target.value);
    };

    const handleImageChange = (e) => {
      if (e.target.files && e.target.files[0]) {
        const file = e.target.files[0];
    
        if (file.size > 1048576) {
          alert('The file size must be less than 1 MB.');
          return;
        }
    
        const reader = new FileReader();
        reader.onload = (e) => {
          setImage(e.target.result);
        };
        reader.readAsDataURL(file);
      }
    };

    const handleSend = async () => {
      if (cooldown > 0) return;
    
      if (!newMessage.trim() && !image) return;
    
      const message = {
        text: newMessage,
        sender: account,
        image,
      };
    
      const { data, error } = await supabase
        .from('messages')
        .insert([message]);
    
      if (error) {
        console.error('Error sending message:', error);
        alert(`Failed to send message: ${error.message}`);
      } else {
        setNewMessage('');
        setImage(null);
        setCooldown(30);
      }
    };
  
    const calculateTopList = (messages) => {
      const userMessageCount = new Map();
    
      messages.forEach((msg) => {
        if (msg.text && msg.text.trim() !== '') {
          userMessageCount.set(msg.sender, (userMessageCount.get(msg.sender) || 0) + 1);
        }
      });
    
      const sortedTopList = Array.from(userMessageCount.entries()).sort((a, b) => b[1] - a[1]);
      setTopList(sortedTopList);
    
      const top10 = sortedTopList.slice(0, 25).map(([user]) => user);
      setTop10Addresses(top10);
    };
  
    const handlePageChange = (page) => {
      setCurrentPage(page);
    };
  
    const getSenderName = (sender) => {
      if (!sender) {
        return 'Unknown';
      }
      
      if (sender === '0xEa9C8f2D9d91cF44206F0d869b9cc39897a13Ae1') {
        return 'Admin';
      }
      
      return names[sender] || `${sender.substring(0, 5)}...${sender.substring(sender.length - 5)}`;
    };
    
    const getSenderStyle = (sender) => {
        if (sender === '0xEa9C8f2D9d91cF44206F0d869b9cc39897a13Ae1') {
            return { color: 'gold' };
        }
        const index = topList.findIndex(([user]) => user === sender);
        if (index >= 0) {
            if (index < 25) {
                return { color: '#ffdc73' };
            }
            if (index < 100) {
                return { color: '#E5E4E2' };
            }
            if (index < 250) {
                return { color: '#cd7f32' };
            }
        }
        return { color: '#fff' };
    };
    
    const getSenderImage = (sender) => {
      const userNumber = userNumbers[sender];
      if (userNumber) {
        const formattedNumber = userNumber < 10 ? `0${userNumber}` : `${userNumber}`;
        return `images/pixel/${formattedNumber}.jpg`;
      }
      return 'images/pixel/200.png';
    };
  
    const formatTimestamp = (timestamp) => {
      const date = new Date(timestamp);
      return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit', second: '2-digit' });
    };
    
  
    const startIndex = (currentPage - 1) * itemsPerPage;
    const endIndex = startIndex + itemsPerPage;
    const currentItems = topList.slice(startIndex, endIndex);
  
    const totalPages = Math.ceil(topList.length / itemsPerPage);
  
    const getPageNumbers = () => {
      const pageNumbers = [];
      const maxPageNumbers = 3;
      const startPage = currentPage;
  
      for (let i = 0; i < maxPageNumbers && startPage + i <= totalPages; i++) {
        pageNumbers.push(startPage + i);
      }
  
      return pageNumbers;
    };
    
    const getActiveIndex = (pathname) => {
      if (
        pathname.includes('/lockedstaking/all') ||
        pathname.includes('/lockedstaking/history')
      ) {
        return 1;
      }
      return 0;
    };
  
    const filteredTopList = topList.filter(([user]) => {
      if (!searchQuery) return true;
      const userName = getSenderName(user);
      return userName.toLowerCase().includes(searchQuery.toLowerCase()) || user.toLowerCase().includes(searchQuery.toLowerCase());
    });
  
    const filteredMessages = messages.filter((msg) =>
      msg.text && msg.text.toLowerCase().includes(searchQuery.toLowerCase())
    );

    return (
      <Container>
        {account ? (
          <>
            <Title>VizslaChat</Title>
            <Flex alignItems="center" justifyContent="center" >
              <ButtonMenu activeIndex={getActiveIndex(location.pathname)} scale="sm" variant="subtle">
                <ButtonMenuItem id="swap-nav-link" to="/chat" as={Link}>
                  {t('Public')}
                </ButtonMenuItem>
                <ButtonMenuItem id="swap-nav-link" to="/privatechat" as={Link} mt="4px">
                  {t('Private')}
                </ButtonMenuItem> 
                <ButtonMenuItem id="swap-nav-link" to="/nameregistration" as={Link} mt="4px">
                  {t('Settings')}
                </ButtonMenuItem> 
              </ButtonMenu>
            </Flex>

            <ChatBox ref={chatBoxRef}>
              {filteredMessages.length > 0 ? (
                filteredMessages
                  .filter((msg) => new Date(msg.timestamp))
                  .map((msg) => (
                    <div key={msg.id} style={styles.messageContainer}>
                      <img src={getSenderImage(msg.sender)} alt="sender" style={styles.avatar} />
                      <div style={styles.message}>
                        <b style={getSenderStyle(msg.sender)}>{getSenderName(msg.sender)}:</b> {msg.text}
                        {msg.image && <img src={msg.image} alt="sent" style={styles.sentImage} />}
                        <span style={styles.timestamp}>{formatTimestamp(msg.timestamp)}</span>
                      </div>
                    </div>
                  ))
              ) : (
                <div style={styles.message}>No messages yet.</div>
              )}
              {giftBalance > 200 && (
                <>
                              <Flex alignItems="left" justifyContent="left">
                  <Button variant='gold' onClick={collectTokens} >
                    Collect: {giftBalance} VIZSLASWAP ğŸ‰
                  </Button>
                                </Flex>
                </>
              )}
            </ChatBox>
    
            <div style={styles.inputRow}>
              <Flex alignItems="center" justifyContent="center">
                <Input
                  type="text"
                  value={isSearching ? searchQuery : newMessage}
                  onChange={isSearching ? handleSearchInputChange : (e) => setNewMessage(e.target.value)}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' && !isSearching && cooldown === 0) {
                      handleSend();
                    }
                  }}
                  style={styles.input}
                  placeholder={isSearching ? 'Search in chatbox...' : (cooldown > 0 ? `Cooldown: ${cooldown}s` : 'Type a message')}
                  disabled={cooldown > 0 && !isSearching}
                />
                <Button onClick={handleSend} style={styles.sendButton} disabled={cooldown > 0}>
                  Send
                </Button>
                <Button onClick={() => setEmojiPickerVisible(!emojiPickerVisible)} style={styles.emojiButton}>
                  ğŸ˜Š
                </Button>
                <Button onClick={toggleSearchMode} style={styles.searchButton}>
                  {isSearching ? 'X' : 'ğŸ”'}
                </Button>
                <Button 
                  onClick={() => fileInputRef.current.click()} 
                  style={styles.fileButton}>
                  ğŸ“¤
                </Button>
              </Flex>
    
              {image && (
                <div style={styles.previewContainer}>
                  <Text style={styles.previewText}>Image selected:</Text>
                  <img src={image} alt="Preview" style={styles.previewImage} />
                </div>
              )}
            </div>
    
            <input
              type="file"
              accept="image/*"
              ref={fileInputRef}
              style={{ display: 'none' }}
              onChange={handleImageChange}
            />
    
    {emojiPickerVisible && (
              <EmojiPicker 
                setNewMessage={setNewMessage} 
                newMessage={newMessage} 
                handleSend={handleSend} 
              />
            )}
                  <Flex alignItems="right" justifyContent="right">

            <Gift />
            </Flex>
            <Flex alignItems="right" justifyContent="right">
            {lastCollector && lastCollectedAmount && (
                    <div style={styles.lastCollectedInfo}>
                      <Text bold fontSize='13px'>Last collected by:</Text>
                      <Text bold fontSize='13px'>{lastCollector}</Text>
                      <Text bold fontSize='13px'>Amount: {lastCollectedAmount} VIZSLASWAP</Text>
                    </div>
                  )}
                              </Flex>
            <div style={styles.topListBox}>
              <TopListTitle>Top List</TopListTitle>
              <Input
                type="text"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                style={styles.input}
                placeholder="Search by name or address"
              />
              <ul style={styles.topList}>
                {filteredTopList.slice(startIndex, endIndex).map(([user, count], index) => (
                  <li key={user} style={getSenderStyle(user)}>
                    <img src={getSenderImage(user)} alt="position" style={styles.topListImage} />
                    {startIndex + index + 1}. {getSenderName(user)}
                    <br />
                    {user}: {count} messages
                  </li>
                ))}
              </ul>
              <div>
                {currentPage > 1 && (
                  <Button onClick={() => handlePageChange(currentPage - 1)} style={styles.paginationButton}>
                    Previous
                  </Button>
                )}
                {getPageNumbers().map((pageNumber) => (
                  <Button
                    key={pageNumber}
                    onClick={() => handlePageChange(pageNumber)}
                    style={styles.paginationButton}
                  >
                    {pageNumber}
                  </Button>
                ))}
                {currentPage < totalPages && (
                  <Button onClick={() => handlePageChange(currentPage + 1)} style={styles.paginationButton}>
                    Next
                  </Button>
                )}
              </div>
            </div>
          </>
        ) : (
          <ConnectWalletButton />
        )}
      </Container>
    );    
};

