import { useEffect } from 'react';
import { Web3Provider } from '@ethersproject/providers';
import WalletConnectProvider from '@walletconnect/web3-provider';

const useWalletConnect = (setProvider: (provider: Web3Provider) => void, disconnectProvider: () => void) => {
    useEffect(() => {
        const initWalletConnect = async () => {
            const provider = new WalletConnectProvider({
                rpc: { 56: 'https://mainnet.infura.io/v3/ae3e4720b890d1efc8745f90a2237732' },
                qrcode: true,
            });

            // Enable the provider and get the web3Provider
            await provider.enable(); 
            const web3Provider = new Web3Provider(provider);

            // Set the provider to state or context
            setProvider(web3Provider);

            // Cleanup on unmount
            return () => {
                if (provider.disconnect) {
                    provider.disconnect();
                }
                disconnectProvider();
            };
        };

        initWalletConnect();

        // Cleanup function
        return () => {
            disconnectProvider();
        };
    }, [setProvider, disconnectProvider]);
};

export default useWalletConnect;
